import React from "react"
import { Link } from "gatsby"
import Navigation from "./navigation";


const ListLink = props => (
  <li style={{ display: `inline-block`, marginRight: `1rem` }}>
    <Link to={props.to}>{props.children}</Link>
  </li>
)

export default ({ children }) => {

  return (
    <div style={{ margin: `0 auto`, maxWidth: 650, padding: `1.25rem 1rem` }}>
      <header style={{ marginBottom: `1.5rem` }}>
        <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
          <h3 style={{ display: `inline` }}>VSLA</h3>
        </Link>
        // <Navigation />
        <ul className="wrapper" id='nav-menu' style={{ listStyle: `none`, float: `right` }}>
          <ListLink className="navigable" to="/">Home</ListLink>
          <ListLink className="navigable" to="/meetings/">Meetings</ListLink>
          <ListLink className="navigable" to="/members/">Members</ListLink>
          <ListLink className="navigable" to="/fund/">Fund</ListLink>
        </ul>
      </header>
      {children}
    </div>
  )
}
